







































































































































































































.template {
  background-color: #fff;
  color: rgb(17, 17, 17);
  width: 100%;
  min-height: 29.6cm;
  word-break: break-all;
  position: relative;
  overflow: hidden;
  --cv-color: #8a75aa;
}
.padding {
  padding: 40px;
}

.logo {
  width: 70%;
}

.img-box {
    width: 120px;
    height: 120px;
    border-radius: 99999px;
    overflow: hidden;
    border: 4px solid #fff;
    z-index: 1;
    position: relative;
    bottom: -25px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.header {
  position: relative;
  .circle {
    position: absolute;
    display: block;
    border-radius: 99999px;
    width: 200px;
    height: 200px;
    transform: scale(2) translateY(-34%);
    background-color: var(--cv-color);
  }
  h1 {
    position: relative;
    margin-top: 20px;
    z-index: 555;
    color: #fff;
    text-transform: capitalize;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 0;
    line-height: 26px;
  }
  p {
    color: rgb(90, 90, 90);
    font-size: 11px;
    font-weight: 500;
    margin-bottom: 0;
  }
}

.rating {
  display: flex;
  span {
    display: block;
    width: 9px;
    height: 9px;
    border-radius: 999px;
    margin-right: 5px;
    margin-bottom: 5px;
    background-color: rgb(174, 174, 174);
    &.active {
      background-color: #2B3067;
    }
  }
}

.skills {
  .skill-name {
    position: relative;
    top: -2px;
    font-size: 11px;
    font-weight: 600;
    margin-bottom: 0;
  }
}

.title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 0.2rem;
}

.description {
  font-size: 10px;
  margin-bottom: 0;
}

.date {
  font-size: 10px;
  font-weight: 500;
  margin-bottom: 0.3rem;
}

.subtitle {
  font-size: 13px;
  font-weight: bold;
  margin-bottom: 0.1rem;
}

.col-left {
  width: 300px;
  overflow: hidden;
  background-color: #EAEAEA;
  flex-shrink: 0;
}

.col-right {
  padding: 30px;
  flex-grow: 1;
}


.languages {
  p {
    font-size: 12px;
    margin-bottom: 0;
    font-weight: 500;
    span {
      font-weight: bold;
    }
  }
}
